




































































import { Vue, Component } from 'vue-property-decorator'
import EditContract from '../components/EditContract.vue'
import Contract from '../components/Contract.vue'
import DB, { Entities } from '../database'
import { ansi16 } from 'color-convert/route'
const FileSaver = require('file-saver-es')

@Component({
    components: {
        Contract,
        EditContract
    }
})
export default class Contracts extends Vue {
    private isloading = true

    private isModalActive = false
    private currentItem: Entities.Contract | null = null
    private contracts: Entities.Contract[] = []
    private isImport: boolean = false

    onSelect(id: number) {
        this.$router.push({
            name: 'contract_detail',
            query: { id: id.toString() }
        })
    }

    async created() {
        this.$ga.page('/inspector/contracts')
        const loading = this.$buefy.loading.open({
            container: null
        })

        await this.list()
        this.prepare()
        loading.close()

        DB.subscribe('contracts', () => {
            this.list()
        })
    }

    mounted() {
        const fileEle = this.$refs.files as HTMLInputElement
        fileEle.onchange = () => {
            const file = fileEle.files && fileEle.files[0]

            if (file) {
                const fr = new FileReader()
                fr.onloadend = event => {
                    const json: Entities.Contract = JSON.parse(
                        (fr.result as string) || ''
                    )
                    if (json) {
                        this.currentItem = {
                            abi: json.abi,
                            address: json.address,
                            name: json.name
                        }
                        const temp = this.contracts.find(
                            contract =>
                                contract.address.toLowerCase() ===
                                json.address.toLowerCase()
                        )
                        if (temp) {
                            this.currentItem.id = temp.id
                        }
                        this.isImport = true
                        this.open()
                    }
                }
                fr.readAsText(file)
            }
        }
    }

    onImport() {
        const fileEle = this.$refs.files as HTMLInputElement
        fileEle.click()
    }

    prepare() {
        const { action, address } = this.$route.query
        switch (action) {
            case 'add':
                this.currentItem = {
                    address
                }
                this.open()
                break
            default:
                break
        }
    }

    reload() {
        (this.$refs.files as HTMLInputElement).value = ''
        this.currentItem = null
        this.list()
        this.isModalActive = false
    }

    private exportJson(item: any) {
        const blob = new Blob(
            [
                JSON.stringify({
                    name: item.name,
                    abi: item.abi,
                    address: item.address
                })
            ],
            { type: 'text/plain' }
        )
        FileSaver.saveAs(blob, `${item.address}.json`)
    }
    private remove(item: any) {
        this.$buefy.dialog.confirm({
            title: 'Remove',
            message: `Are you sure want to remove ${item.name} contract`,
            cancelText: 'Cancel',
            confirmText: 'YES',
            type: 'is-danger',
            scroll: 'clip',
            onConfirm: () => {
                DB.contracts.delete(item.id).then(() => {
                    this.reload()
                })
            }
        })
    }
    private open() {
        this.isModalActive = true
    }
    private close() {
        (this.$refs.files as HTMLInputElement).value = ''
        this.isModalActive = false
    }
    private async list() {
        this.contracts = await DB.contracts.toArray()
        this.isloading = false
    }
    private addItem() {
        this.currentItem = null
        this.isImport = false
        this.open()
    }
    private onCancel() {
        this.currentItem = null
        this.close()
    }
    private edit(item: Entities.Contract) {
        this.currentItem = item
        this.isImport = false
        this.open()
    }
}
